import MvList from '@/components/common/mvlist/mvlist.vue'
import CommentData from '@/components/common/comment/comment.vue'
export default {
	components: {
		MvList,
		CommentData
	},
	data() {
		return {
			//mvId
			mvId: 0,
			//mv播放url数据
			mvUrl: {},
			//mv信息
			mvDetail: {},
			//类似mv信息
			simiMv: {},
			//mv的评论
			commentMv: {}
		}
	},
	onLoad(option) {
		this.mvId = option.mvid
		this.getCurrentMvData(option.mvid)
	},
	methods: {
		//获取当前点击mv数据
		getCurrentMvData(mvid) {
			const allData = [{
				url: '/mv/url?id=',
				data: 'mvUrl'
			}, {
				url: '/mv/detail?mvid=',
				data: 'mvDetail'
			}, {
				url: '/simi/mv?mvid=',
				data: 'simiMv'
			}, {
				url: '/comment/mv?id=',
				data: 'commentMv'
			}]
			//统一获取需要的数据
			allData.forEach((item, index) => {
				this.$request({
					url: item.url + mvid
				}).then(res => {
					this.$set(this, item.data, res);
				})
			})
		},
		//开始播放事件
		mvPlay() {
			if (!this.$music.paused) this.$music.pause()
		}
	}
}
